<template>
  <div class="warps">
    <my-el-header/>
    <div class="container">
        <div class="phone-r">
          <div class="nav-form">
            <el-form :inline="true" :model="form" ref="form" class="demo-form-inline">
              <el-form-item label="" prop="directoryName">
                <el-input v-model="form.directoryName" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="domain">
                <el-input v-model="form.domain" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="billingType">
                <el-select v-model="form.billingType" placeholder="">
                  <el-option
                    :label="item.label"
                    :value="item.value"
                    v-for="item in billingType"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="find"></el-button>
                <el-button @click="clear"></el-button>
              </el-form-item>
            </el-form>
            <el-button type="primary" @click="addForms(null, '')"
            >
            </el-button
            >
          </div>

          <el-table
            :row-class-name="tableRowClassName"
            :header-cell-style="{background:'#ccc', color: '#fff',}"
            :data="list"
            style="width: 100%"
            height="calc(100vh - 100px - 100px - 100px - 100px)"
            v-if="list.length !== 0"
          >
            <el-table-column prop="date" align="center" label="" width="50px">
              <template scope="scope">
                {{ scope.$index + 1 }}
              </template>
            </el-table-column>
            <el-table-column align="center" prop="directoryName" label=""></el-table-column>
            <el-table-column align="center" prop="directoryNumber" label="">
            </el-table-column>
            <el-table-column align="center" prop="diaplan" label="">
            </el-table-column>
            <el-table-column align="center" prop="deptName" label="">
            </el-table-column>
            <el-table-column align="center" prop="groupName" label="">
            </el-table-column>
            <el-table-column align="center" prop="domain" label=""></el-table-column>
            <el-table-column align="center" prop="balance" label=""></el-table-column>
            <el-table-column align="center" prop="expire" label=""></el-table-column>
            <el-table-column align="center" prop="overdraft" label="">
              <template scope="scope">
                <div v-if="scope.row.billingType === 1"></div>
                <div v-else></div>
              </template>
            </el-table-column>
            <el-table-column align="center" prop="bussiness" label=""> </el-table-column>
            <el-table-column align="center" prop="address" label="">
              <template scope="scope">
                <el-link
                  class="a-link"
                  type="info"
                  @click="addForms(scope.row, '')"
                >
                </el-link
                >

                <el-link class="a-link" type="info" @click="recharge(scope.row)"
                >
                </el-link
                >

                <template>
                  <el-popconfirm
                    title=""
                    @confirm="removeIt(scope.row)"
                  >
                    <el-link class="a-link" type="info"  slot='reference'
                    >
                    </el-link
                    >
                  </el-popconfirm>
                </template>
              </template>
            </el-table-column>
          </el-table>
          <my-empty v-else />

        </div>

        <el-dialog :title="title" :visible.sync="dialogFormVisible" destroy-on-close>
          <el-form :model="addForm" :rules="rules" ref="addForm">
            <div class="width">
              <el-form-item

                label=""
                :label-width="formLabelWidth"
                prop="directoryName"
              >
                <el-input
                  placeholder=""
                  v-model="addForm.directoryName"
                  autocomplete="off"
                >
                </el-input>
              </el-form-item>
              <el-form-item
                label=""
                :label-width="formLabelWidth"
                prop="accountUser"
              >
                <el-input
                  placeholder=""
                  @change="accountUserChange"
                  v-model="addForm.accountUser"
                  autocomplete="off"
                >
                </el-input>
              </el-form-item>

            </div>

            <div class="width">
              <el-form-item
                label=""
                :label-width="formLabelWidth"
                prop="billingType"
              >
                <el-select v-model="addForm.billingType" style="width: 100%" >
                  <el-option
                    v-for="item in billingType"
                    :key="item.id"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item
                label=""
                :label-width="formLabelWidth"
                prop="bussiness"
              >
                <!--            <el-select v-model="addForm.bussiness">-->
                <!--              <el-option :label="item.label" :value="item.value" v-for="item in bussiness"></el-option>-->
                <!--            </el-select>-->
                <el-select
                  v-model="bussinessList"
                  multiple
                  @change="change"
                  style="width: 100%"
                  collapse-tags
                  placeholder="">
                  <el-option
                    v-for="item in bussiness"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </div>
            <div class="width">
              <el-form-item
                label=""
                :label-width="formLabelWidth"
                prop="directoryNumber"
              >
                <el-input
                  :disabled="true"
                  v-model="addForm.directoryNumber"
                  autocomplete="off"
                ></el-input>
              </el-form-item>
              <el-form-item
                label=""
                :label-width="formLabelWidth"
                prop="password"
              >
                <el-input
                  placeholder=""
                  v-model="addForm.password"
                  clearable
                >
                </el-input>
              </el-form-item>
            </div>
            <div class="width">
              <el-form-item label="" :label-width="formLabelWidth" prop="balance">
                <el-input v-model="addForm.balance" placeholder=""></el-input>
              </el-form-item>

              <el-form-item label="" :label-width="formLabelWidth" prop="domain">
                <el-input v-model="addForm.domain" placeholder=""></el-input>
              </el-form-item>
            </div>
            <div class="width">
              <el-form-item label="" :label-width="formLabelWidth" prop="disable">
                <el-select v-model="addForm.disable" placeholder="" style="width: 100%" >
                  <el-option
                    v-for="item in disable"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth" prop="deptId">
                <my-tree ref="myTree" style="width: 100%"  :options="deptIdList" @getValue="getSelectedValue"></my-tree>
              </el-form-item>
            </div>
            <div class="width">
              <el-form-item label="" :label-width="formLabelWidth" prop="diaplan">
                <el-select v-model="addForm.diaplan" placeholder="" style="width: 100%" >
                  <el-option
                    v-for="item in diaPlanList"
                    :key="item.value"
                    :label="item.diaplanName"
                    :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth" prop="rateGroup">
                <el-select v-model="addForm.rateGroup" placeholder="" style="width: 100%" >
                  <el-option
                    v-for="item in rateGroup"
                    :key="item.value"
                    :label="item.groupName"
                    :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
            </div>
            <div class="width">
              <el-form-item label="" :label-width="formLabelWidth" prop="expire">
                <el-input
                  style="display: inline-block; margin-right: 10px"
                  placeholder=""
                  v-model="addForm.expire"
                ></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth" prop="type">
                <el-select v-model="addForm.type" placeholder="" style="width: 100%" >
                  <el-option
                    v-for="item in typeList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </div>
            <div class="width">
              <el-form-item label="" :label-width="formLabelWidth" prop="latitude">
                <el-input
                  placeholder=""
                  v-model="addForm.latitude"
                ></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth" prop="longitude">
                <el-input v-model="addForm.longitude" placeholder=""></el-input>
              </el-form-item>
            </div>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"></el-button>
            <el-button type="primary" @click="submitForm('addForm')"
            > 
            </el-button
            >
          </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="showBar" width="30%" center>
          <p class="title">
            <b></b>
            <span
            > {{ rechargeInfo.directoryNumber }}</span
            >
          </p>
          <el-input
            v-model="chongZhi.balance"
            placeholder=""
          ></el-input>
          <span slot="footer" class="dialog-footer">
        <el-button @click="showBar = false"> </el-button>
        <el-button type="primary" @click="submit(rechargeInfo)"
        > </el-button
        >
      </span>
        </el-dialog>
      </div>
    <my-footer v-on:next = "next" @prev="prev" :form="form" @change="footerChange"></my-footer>
  </div>
</template>

<script>
import eTree from "../../../components/eTree.vue";
import { getOrganizeList } from "@/newwork/system-colltroner";
import { diaPlanList, getRateList } from "@/newwork/ground-colltroner";
import { fn } from "@/uti";
import { addDirectory, deleteDirectory, getDirectory, recharge, upDateDirectory } from "@/newwork/directory";
import myEmpty from "@/newwork/myEmpty";
import { isValidIP, isValidNumber } from "@/util/validate";
import myTree from "@/components/myTree";
import myFooter from "@/components/myFooter";
import myElHeader from "@/components/myElHeader";

export default {
  name: "phoneConfig",
  components: {
    eTree,
    myEmpty,
    myTree,
    myFooter,
    myElHeader
  },
  computed: {
    getterDeptIdList() {
      return fn(this.deptIdList);
    },

  },
  data() {
    const validateIP = (rule, value, callback) => {
      if (!isValidIP(value)) {
        callback(new Error("ip,"));
      } else {
        callback();
      }
    };
    const validateNum = (rule, value, callback) => {
      if (!isValidNumber(value)) {
        callback(new Error("ip,"));
      } else {
        callback();
      }
    };

    return {
      title: "",
      dialogFormVisible: false,
      formLabelWidth: "150px",
      showBar: false,
      form: {
        directoryName: "",
        domain: "",
        billingType: ""
      },
      deptId: "",//ID
      diaPlanList: [],//
      rechargeInfo: {
        value: null
      },
      bussinessList: [],
      chongZhi: {},
      addForm: {
        domain: "", //
        accountUser: "", //
        balance: "", // balance
        billingType: "", //12
        bussiness: "", //
        deptId: "", // ID
        diaplan: "", //
        directoryName: "", //
        // stats: '', //
        directoryNumber: "", //
        disable: "", //01disable
        // domain: '', //
        endpointId: "45", //ID
        expire: "", //
        latitude: "",  //
        longitude: "", //
        password: "", //
        rateGroup: "", //
        type: "" //:01:23:456:7:8:
      },
      list: [],
      deptIdList: [], //
      rateGroup: [],//
      typeList: [
        { label: "", value: 0 },
        { label: "", value: 1 },
        { label: "", value: 2 },
        { label: "", value: 3 },
        { label: "", value: 4 },
        { label: "", value: 5 },
        { label: "", value: 6 },
        { label: "", value: 7 },
        { label: "", value: 8 }
      ],
      billingType: [
        { label: "", value: 1 },
        { label: "", value: 2 }
      ],
      relayType: [
        { label: "", value: "" },
        { label: "", value: "" }
      ],
      rules: {
        accountUser: [{ required: true, message: ",", trigger: "blur" }],
        directoryName: [{ required: true, message: ",", trigger: "blur" }],
        billingType: [{ required: true, message: ",", trigger: "blur" }],
        bussiness: [{ required: true, message: ",", trigger: "blur" }],
        directoryNumber: [{ required: true, message: ",", trigger: "blur" },
          { validator: validateNum, message: "", trigger: "blur" }
        ],
        password: [{ required: true, message: ",", trigger: "blur" }],
        balance: [{ required: true, message: ",", trigger: "blur" },
          { validator: validateNum, message: "", trigger: "blur" }],
        domain: [
          { required: true, message: ",", trigger: "blur" },
          { validator: validateIP, message: "IP, ", trigger: "blur" }
        ],
        disable: [{ required: true, message: ",", trigger: "blur" }],
        deptId: [{ required: true, message: ",", trigger: "blur" }],
        diaplan: [{ required: true, message: ",", trigger: "blur" }],
        rateGroup: [{ required: true, message: ",", trigger: "blur" }],
        expire: [{ required: true, message: ",", trigger: "blur" },
          { validator: validateNum, message: "", trigger: "blur" }
        ],
        type: [{ required: true, message: ",", trigger: "blur" }],
        latitude: [{ required: false, message: ",", trigger: "blur" }],
        longitude: [{ required: false, message: ",", trigger: "blur" }]
      },
      bussiness: [
        { label: "", value: "" },
        { label: "", value: "" },
        { label: "", value: "" }
      ],
      disable: [
        { label: "", value: 0 },
        { label: "disable", value: 1 }
      ]
    };
  },
  methods: {
    next(){
      this.form.pageNum ++
      console.log(this.form);
      this.getDirectory(this.form)
    },
    prev(){
      this.form.pageNum --
      this.getDirectory(this.form)
    },
    footerChange(e){
      console.log(e);
      this.form.pageNum = e
      this.getDirectory(this.form)
    },
    find() {
      this.getDirectory(this.form);
    },
    getSelectedValue(value) {
      this.addForm.deptId = value.deptId;
    },
    clear() {
      this.form = this.$options.form
      this.getDirectory(this.form);
    },
    change(){
      this.addForm.bussiness = this.bussinessList.join(',')
    },
    accountUserChange(){
      this.addForm.directoryNumber = this.addForm.accountUser
    },
    submitForm() {
      this.$refs["addForm"].validate((valid) => {
        if (valid) {
          if (this.title !== "") {
            addDirectory(this.addForm).then(res => {
              if (res.data.code === 200) {
                this.$message.success("");
                this.getDirectory(this.form);
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.data.msg);
              }
            }).catch(e => this.$message.error(e));
          } else {
            upDateDirectory(this.addForm).then(res => {
              if (res.data.code === 200) {
                this.getDirectory(this.form);
                this.$message.success("");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.data.msg);
              }
            });
          }
        } else {
          this.$message.error(" ");
        }
      });
    },
    addForms(row, title) {
      this.dialogFormVisible = true;
      this.title = title;
      if (title === "" && this.dialogFormVisible === true) {
        this.addForm = row;
        this.$nextTick(() => {
          this.$refs.myTree.valueName = this.addForm.deptName
          this.bussinessList =  this.addForm.bussiness.split(',')
        })
      } else {
        this.addForm = this.$options.data().addForm;
        this.$nextTick(() => {
          this.$refs.myTree.valueName = this.addForm.deptName
          this.bussinessList =  []
        })
      }
    },
    tableRowClassName({ row, rowIndex }) {
      row.index = rowIndex;
    },
    removeIt(row) {
      deleteDirectory(row.id).then(res => {
        if (res.data.code === 200) {
          this.$message.success("");
          this.getDirectory(this.form);
        } else {
          this.$message.error(res.data.msg);
        }
      }).catch(e => this.$message.error(e));
    },
    resetForm() {
      this.$refs.form.resetFields();
    },
    // string === 'form' ? this.$refs.form.resetFields() :
    recharge(row) {
      this.showBar = true;
      this.rechargeInfo = row;
      this.chongZhi.id = row.id;
    },
    submit() {
      recharge(this.chongZhi).then(res => {
        if (res.data.code === 200) {
          this.getDirectory(this.form);
          this.$message.success("");
          this.chongZhi = {};
        } else {
          this.$message.error(res.data.data);
        }
      }).catch(e => this.$message.error(e));
      this.showBar = false;
    },
    getDiaPlanList(form) {
      diaPlanList(form).then(res => {
        if (res.data.code === 200) {
          this.list = res.data.data.records;
        } else {
          this.$message.error(res.data.msg);
        }
      }).catch(e => this.$message.error(e));
    },
    getDirectory(form) {
      getDirectory(form).then(res => {
        this.$bus.$emit('total', res.data.data.total)
        if (res.data.code === 200) {
          this.list = res.data.data.records;
        }
      }).catch(e => this.$message.error(e));
    }

  },

  created() {
    this.getDirectory(this.form);
  },
  watch: {
    dialogFormVisible(val, newVal) {
      if (val) {
        getOrganizeList().then(res => {
          this.deptIdList = fn(res.data.data);
        }).catch(e => this.$message.error(e));

        /**
         *
         * 
         */
        diaPlanList().then(res => {
          this.diaPlanList = res.data.data.records;
        }).catch(e => this.$message.error(e));


      } else {
        this.resetForm();
        this.deptIdList = [];
        this.getDirectory(this.form);
      }
      /**
       *
       * 
       */
      getRateList().then(res => {
        this.rateGroup = res.data.data.records;
      }).catch(e => this.$message.error(e));
    },
    deptId(val) {
      this.addForm.deptId = val[val.length - 1];
    }
  }

};
</script>

