<template>
  <!--  -->
  <div class="role">
    <el-header>
      <div class="content-nav">
        <el-breadcrumb class="nav-bar" separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item><a>{{ $route.meta.title }}</a></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    </el-header>
    <div class="container">
      <div class="nav">
        <div class="nav-l">
          <el-form ref="form" label-width="80px" :inline="true" :rules="form_rules"  :model="navForm">
            <el-form-item label="" prop="roleName">
              <el-input v-model="navForm.roleName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="status">
              <el-select v-model="navForm.status" placeholder="">
                <el-option
                    v-for="item in status"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <el-button type="primary" @click="find"></el-button>
          <el-button @click="clearForm"></el-button>
        </div>
        <div class="nav-r">
          <el-button type="primary" @click="addForms(null, '')"></el-button>
        </div>
      </div>

      <el-dialog title="" :visible.sync="dialogFormVisibles">
        <el-form :model="dataScopeForm">
          <el-form-item label="ID" :label-width="formLabelWidth">
            <el-input v-model="dataScopeForm.roleName" placeholder=""/>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="dataScopeForm.dataScope">
              <el-option :label="item.label" :value="item.value" v-for="item in dataScopeType"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" required>
            <el-tree
              props="permission"
              :data="menuIds"
              :default-checked-keys="defaultMenuIds"
              :props="defaultProps"
              node-key="menuId"
              accordion
              :check-on-click-node="true"
              show-checkbox
              @check="checkChange">
            </el-tree>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisibles = false"> </el-button>
          <el-button type="primary" @click="submitDataScopeForm(dataScopeForm)"> </el-button>
        </div>
      </el-dialog>


      <el-dialog :title="title" :visible.sync="dialogFormVisible" destroy-on-close>
        <el-form :model="addForm" ref="addForm" :rules="rules">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="roleName"
          >
            <el-input v-model="addForm.roleName" autocomplete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="orderNum"
          >
            <el-input v-model="addForm.orderNum" autocomplete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="roleCode"
          >
            <el-input v-model="addForm.roleCode" autocomplete="off" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth">
            <el-radio-group v-model="addForm.status">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="addForm.desc" autocomplete="off" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm"
          > 
          </el-button
          >
        </div>
      </el-dialog>

      <el-table
          max-height="800px"
          v-if="list.length !== 0"
          :data="list"
          :header-cell-style="{background:'#f2f2f2'}"
          height="500px"
          style="width: 100%; margin-top: 20px">
        <el-table-column
            prop="date"
            align="center"
            label=""
            width="180">
          <template scope="scope">{{ scope.$index + 1 }}</template>
        </el-table-column>
        <el-table-column
            prop="roleName"
            align="center"
            label=""
            width="180">
        </el-table-column>
        <el-table-column
            prop="name"
            align="center"
            label=""
            width="180">
          <template scope="scope">
            <p v-if="scope.row.status === 0">
              
            </p>
            <p v-else>
              
            </p>
          </template>
        </el-table-column>
        <el-table-column
            prop="createTime"
            align="center"
            label=""
            width="300">
        </el-table-column>
        <el-table-column
            prop="createTime"
            align="center"
            label=""
            width="180">
          <template scope="scope">
            <el-link @click="distribution(scope.row)" type="info"></el-link>
          </template>
        </el-table-column>
        <el-table-column
            align="center"
            prop="address"
            label="">
          <template scope="scope">
            <el-link style="margin-right: 20px" type="info" @click="addForms(scope.row, '')"></el-link>
            <el-link style="margin-right: 20px" type="info" @click="removeIt(scope.row)"></el-link>
          </template>
        </el-table-column>
      </el-table>
      <my-empty v-else></my-empty>
    </div>
    <my-footer v-on:next = "next" @prev="prev" :form="navForm" @change="change"></my-footer>
  </div>
</template>

<script>
import {
  addRole,
  getRoleList,
  getMenuAll,
  deleteRoleList,
  getDistribution,
  distribution, upDataRoleList
} from "@/newwork/system-colltroner";
import {menuToTree, treeToArray} from "@/uti";
import {deleteRate} from "@/newwork/ground-colltroner";
import myEmpty from "@/newwork/myEmpty";
import {isValidNumber} from "@/util/validate";
import myFooter from "@/components/myFooter";


export default {
  name: 'role-manage',
  data() {
    const validateNum = (rule, value, callback) => {
      if(!isValidNumber(value)){
        callback(new Error('ip,'))
      }else {
        callback()
      }
    }
    return {
      menuIds: [],//
      formLabelWidth: '120px',
      dialogFormVisible: false,
      dialogFormVisibles: false,
      title: '',
      navForm: {
        pageSize: 14,
        pageNum: 1,
        orderNum: '', //
        roleCode: '', //
        roleName: '', //
        status: '',
      },
      defaultMenuIds: [], // 'ID'
      timer: {},
      addForm: {
        menuIds: [],
        pageSize: 14,
        pageNum: 1,
        orderNum: '', //
        roleCode: '', //
        roleName: '', //
        status: 0,
      },
      total: 0,
      selectMenuList: [],
      dataScopeForm: {
        dataScope: ''
      },
      list: [],
      rules: {
        roleName: [{required: true, message: '', trigger: 'blur'},],
        orderNum: [{required: true, message: '', trigger: 'blur'},
          {validator: validateNum, message: '', trigger: 'blur'}
        ],
        roleCode: [{required: true, message: '', trigger: 'blur'},],
        permission: [
          {required: true, message: '', trigger: 'blur'}
        ],
        status: [
          {required: true, message: '', trigger: 'blur'}
        ]
      },
      form_rules:{
        roleName: [{required: false, message: '', trigger: 'blur'},],
        status: [
          {required: false, message: '', trigger: 'blur'}
        ]
      },
      isVacancy: true,
      defaultProps: {
        children: 'children',
        label: 'menuName'
      },
      status: [
        {label: '', value: 0},
        {label: '', value: 1},
      ],
      dataScopeType: [
        {label: '', value: 1},
        {label: '', value: 2},
        {label: '', value: 3},
        {label: '', value: 4},
        {label: '', value: 5},
      ]


    }
  },
  watch: {
    dialogFormVisibles(val) {
      if (val) {
        getMenuAll().then(res => {
          this.menuIds = menuToTree(res.data.data)
        }).catch(e => {
          this.$message.error(e)
        })
        // 
        if(this.title === ''){
          this.dataScopeForm.sysMenuList.forEach(item => {
            this.defaultMenuIds.push(item.menuId)
            console.log(this.defaultMenuIds);
          })
          this.dataScopeForm.menuIds = this.defaultMenuIds
        }
      }else {
        this.defaultMenuIds = []
      }}
  },
  methods: {
    next(){
      this.navForm.pageNum --
      this.getRoleList(this.navForm)
    },
    prev(){
      this.navForm.pageNum ++
      this.getRoleList(this.navForm)
    },
    change(e){

      this.navForm.pageNum = e
      this.getRoleList(this.navForm)
    },
    distribution(row) {
      this.dialogFormVisibles = true
      this.dataScopeForm.roleId = row.roleId
      this.dataScopeForm = row
    },
    submitDataScopeForm(dataScopeForm){
      distribution(dataScopeForm).then(res => {
        console.log(res);
        if(res.data.code === 200){
          this.$message.success('')
          this.getRoleList(this.navForm)
          this.dialogFormVisibles = false
        }else {
          this.$message.error(res.data.msg)
        }
      })
    },
    addForms(row, title) {
      this.dialogFormVisible = true
      this.title = title
      this.title === '' ? this.addForm = this.$options.data().addForm : this.addForm = row
    },
    find() {
      this.getRoleList(this.navForm)
    },
    clearForm() {
      this.navForm = this.$options.data().navForm
      this.getRoleList(this.navForm)
    },
    removeIt(row) {
      deleteRoleList(row.roleId).then(res => {
        if (res.data.code === 200) {
          this.$message.success('')
          this.getRoleList(this.navForm)
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    submitForm() {
      this.$refs.addForm.validate((valid) => {
        this.changeAddForm()
        if (valid && !this.isVacancy) {
          if (this.title === '') {
            addRole(this.addForm).then(res => {
              if (res.data.code === 200) {
                this.$message.success('')
                this.getRoleList(this.navForm)
                this.resetForm()
                this.dialogFormVisible = false
              } else {
                this.$message.error(res.data.msg)
              }
            }).catch(e => this.$message.error(e))
          } else {
            upDataRoleList(this.addForm).then((res => {
              if(res.data.code === 200){
                this.$message.success('')
                this.getRoleList(this.navForm)
                this.dialogFormVisible = false
              }else {
                this.$message.error(res.data.msg)
              }
            }))
          }
        } else {
          return false;
        }
      });
    },
    resetForm(string) {
      string === 'form' ? this.$refs.form.resetFields() : this.$refs.addForm.resetFields();
      this.getRoleList(this.navForm)
    },

    checkChange(a, context) {
      this.addForm.menuIds = []
      const menuId = treeToArray(context.checkedNodes)

      // 
      menuId.forEach(item =>  this.addForm.menuIds.push(item.menuId))
    this.changeAddForm()
    },
    /**
     *
     *   
     */
    changeAddForm(){
      this.addForm.menuIds.length === 0 ? this.isVacancy = true : this.isVacancy = false
    },
    getRoleList(form) {
      getRoleList(form).then(res => {
        this.$bus.$emit('total', res.data.data.total)
        this.list = res.data.data.records
      }).catch(e => {
        this.$message.error(e)
      })
    }
  },
  created() {
    this.getRoleList(this.navForm)
  },


  components: {
    myEmpty,
    myFooter
  },
}
</script>

<style >
.el-header{
  padding: 0 !important;
}
.role{
  display: flex;
  height: calc(100vh - 160px);
  justify-content: space-between;
  flex-direction: column;
}
.content-nav {
  padding: 20px;
  box-shadow: 0 0 15px #ccc;
  border-radius: 10px;
  background-color: #fff;
}
.el-table::before{
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0;
}
.nav {
  display: flex;
  justify-content: space-between;
  height: 40px;
  overflow: hidden;
}

.nav-l {
  height: 40px;
  display: flex;
}
.container{
  margin-top: 20px;
  width: 100%;
  padding: 20px;
  box-shadow: 0 0 15px #ccc;
  background-color: #fff;
  border-radius: 10px;
  flex: 1;
}
</style>
